<template>
    <div class="column">
        <div class="tabs is-toggle is-fullwidth">
            <ul>
                <li :class="{'is-active': summaryRevenueIsActive}" @click="updateIsActiveIndex(0)">
                    <a>
                        <div class="has-text-centered">
                            <p>_</p>
                            <p class="is-size-4"> All </p>
                            <p>_</p>
                        </div>
                    </a>
                </li>
                <li :class="{'is-active': todayRevenueIsActive}" @click="updateIsActiveIndex(1)">
                    <a>
                        <div class="has-text-centered">
                            <p>Today</p>
                            <p class="is-size-4">$ {{ todayRevenueTotal }} </p>
                            <p>{{ todayOrdersTotal }} Orders</p>
                        </div>
                    </a>
                </li>
                <li :class="{'is-active': yesterdayRevenueIsActive}" @click="updateIsActiveIndex(2)">
                    <a>
                        <div class="has-text-centered">
                            <p>Yesterday</p>
                            <p class="is-size-4">$ {{ yesterdayRevenueTotal }}</p>
                            <p>{{ yesterdayOrdersTotal }} Orders</p>
                        </div>
                    </a>
                </li>
                <li :class="{'is-active': weekRevenueIsActive}" @click="updateIsActiveIndex(3)">
                    <a>
                        <div class="has-text-centered">
                            <p>Week</p>
                            <p class="is-size-4">$ {{ weekRevenueTotal }}</p>
                            <p>{{ weekOrdersTotal }} Orders</p>
                        </div>
                    </a>
                </li>
                <li :class="{'is-active': monthRevenueIsActive}" @click="updateIsActiveIndex(4)">
                    <a>
                        <div class="has-text-centered">
                            <p>Month</p>
                            <p class="is-size-4">$ {{ monthRevenueTotal }}</p>
                            <p>{{ monthOrdersTotal }} Orders</p>
                        </div>
                    </a>
                </li>
                <li :class="{'is-active': quarterRevenueIsActive}" @click="updateIsActiveIndex(5)">
                    <a>
                        <div class="has-text-centered">
                            <p>Quarter</p>
                            <p class="is-size-4">$ {{ quarterRevenueTotal }}</p>
                            <p>{{ quarterOrdersTotal }} Orders</p>
                        </div>
                    </a>
                </li>
                <li :class="{'is-active': annualRevenueIsActive}" @click="updateIsActiveIndex(6)">
                    <a>
                        <div class="has-text-centered">
                            <p>Year</p>
                            <p class="is-size-4">$ {{ annualRevenueTotal }}</p>
                            <p>{{ annualOrdersTotal }} Orders</p>
                        </div>
                    </a>
                </li>
            </ul>
        </div> 
        <revenuecharts-component :is-active-index="currentIsActiveIndex"></revenuecharts-component>       
    </div>
</template>
<script>
import RevenueChartsComponent from "@/components/RevenueChartsComponent"

export default {
    name: "RevenueDashboardComponent",
    data() {
        return {
            currentIsActiveIndex: 0,
        }
    },
    methods: {
        updateIsActiveIndex(index) {
            this.currentIsActiveIndex = index;
        }
    },
    computed: {
        summaryRevenueIsActive() {
            return this.currentIsActiveIndex === 0;
        },
        todayRevenueIsActive() {
            return this.currentIsActiveIndex === 1;
        },
        yesterdayRevenueIsActive() {
            return this.currentIsActiveIndex === 2;
        },
        weekRevenueIsActive() {
            return this.currentIsActiveIndex === 3;
        },
        monthRevenueIsActive() {
            return this.currentIsActiveIndex === 4;
        },
        quarterRevenueIsActive() {
            return this.currentIsActiveIndex === 5;
        },
        annualRevenueIsActive() {
            return this.currentIsActiveIndex === 6;
        },
        todayRevenueTotal() {
            return this.$store.state.cashier.todayRevenueTotal;
        },
        todayOrdersTotal() {
            return this.$store.state.cashier.todayOrdersTotal;
        },
        yesterdayRevenueTotal() {
            return this.$store.state.cashier.yesterdayRevenueTotal;
        },
        yesterdayOrdersTotal() {
            return this.$store.state.cashier.yesterdayOrdersTotal;
        },
        weekRevenueTotal() {
            return this.$store.state.cashier.weekRevenueTotal;
        },
        weekOrdersTotal() {
            return this.$store.state.cashier.weekOrdersTotal;
        },
        monthRevenueTotal() {
            return this.$store.state.cashier.monthRevenueTotal;
        },
        monthOrdersTotal() {
            return this.$store.state.cashier.monthOrdersTotal;
        },
        quarterRevenueTotal() {
            return this.$store.state.cashier.quarterRevenueTotal;
        },
        quarterOrdersTotal() {
            return this.$store.state.cashier.quarterOrdersTotal;
        },
        annualRevenueTotal() {
            return this.$store.state.cashier.annualRevenueTotal;
        },
        annualOrdersTotal() {
            return this.$store.state.cashier.annualOrdersTotal;
        },
    },
    components: {
        "revenuecharts-component": RevenueChartsComponent
    }
}
</script>