<script>
export default {
    props: ["isActiveIndex"],
    data() {
        return {
            currentIsActiveIndex: this.isActiveIndex 
        }
    },
    mounted() {
        google.charts.load("current", {packages:["corechart"]});
        google.charts.setOnLoadCallback(drawChart);
        let todayRevenueData = this.$store.state.cashier.todayRevenueData;
        let todayRevenueOptions = this.$store.state.cashier.todayRevenueOptions;
        let todayOrdersData = this.$store.state.cashier.todayOrdersData;
        let todayOrdersOptions = this.$store.state.cashier.todayOrdersOptions;

        let yesterdayRevenueData = this.$store.state.cashier.yesterdayRevenueData;
        let yesterdayRevenueOptions = this.$store.state.cashier.yesterdayRevenueOptions;
        let yesterdayOrdersData = this.$store.state.cashier.yesterdayOrdersData;
        let yesterdayOrdersOptions = this.$store.state.cashier.yesterdayOrdersOptions;

        let weekRevenueData = this.$store.state.cashier.weekRevenueData;
        let weekRevenueOptions = this.$store.state.cashier.weekRevenueOptions;
        let weekOrdersData = this.$store.state.cashier.weekOrdersData;
        let weekOrdersOptions = this.$store.state.cashier.weekOrdersOptions;

        let monthRevenueData = this.$store.state.cashier.monthRevenueData;
        let monthRevenueOptions = this.$store.state.cashier.monthRevenueOptions;
        let monthOrdersData = this.$store.state.cashier.monthOrdersData;
        let monthOrdersOptions = this.$store.state.cashier.monthOrdersOptions;

        let quarterRevenueData = this.$store.state.cashier.quarterRevenueData;
        let quarterRevenueOptions = this.$store.state.cashier.quarterRevenueOptions;
        let quarterOrdersData = this.$store.state.cashier.quarterOrdersData;
        let quarterOrdersOptions = this.$store.state.cashier.quarterOrdersOptions;

        let annualRevenueData = this.$store.state.cashier.annualRevenueData;
        let annualRevenueOptions = this.$store.state.cashier.annualRevenueOptions;
        let annualOrdersData = this.$store.state.cashier.annualOrdersData;
        let annualOrdersOptions = this.$store.state.cashier.annualOrdersOptions;
        function drawChart() {
            let _todayRevenueData = google.visualization.arrayToDataTable(todayRevenueData);
            let _todayOrdersData = google.visualization.arrayToDataTable(todayOrdersData);
            let todayOrdersChart = new google.visualization.ColumnChart(document.getElementById('todayOrdersChart'));
            todayOrdersChart.draw(_todayOrdersData, todayOrdersOptions);
            let todayRevenueChart = new google.visualization.PieChart(document.getElementById('todayRevenueChart'));
            todayRevenueChart.draw(_todayRevenueData, todayRevenueOptions);

            let _yesterdayRevenueData = google.visualization.arrayToDataTable(yesterdayRevenueData);
            let yesterdayRevenueChart = new google.visualization.PieChart(
                document.getElementById('yesterdayRevenueChart')
            );
            yesterdayRevenueChart.draw(_yesterdayRevenueData, yesterdayRevenueOptions);
            let _yesterdayOrdersData = google.visualization.arrayToDataTable(yesterdayOrdersData);
            let yesterdayOrdersChart = new google.visualization.ColumnChart(
                document.getElementById('yesterdayOrdersChart')
            );
            yesterdayOrdersChart.draw(_yesterdayOrdersData, yesterdayOrdersOptions);

            let _weekRevenueData = google.visualization.arrayToDataTable(weekRevenueData);
            let weekRevenueChart = new google.visualization.PieChart(
                document.getElementById('weekRevenueChart')
            );
            weekRevenueChart.draw(_weekRevenueData, weekRevenueOptions);
            let _weekOrdersData = google.visualization.arrayToDataTable(weekOrdersData);
            let weekOrdersChart = new google.visualization.ColumnChart(
                document.getElementById('weekOrdersChart')
            );
            weekOrdersChart.draw(_weekOrdersData, weekOrdersOptions);

            let _monthRevenueData = google.visualization.arrayToDataTable(monthRevenueData);
            let monthRevenueChart = new google.visualization.PieChart(
                document.getElementById('monthRevenueChart')
            );
            monthRevenueChart.draw(_monthRevenueData, monthRevenueOptions);
            let _monthOrdersData = google.visualization.arrayToDataTable(monthOrdersData);
            let monthOrdersChart = new google.visualization.ColumnChart(
                document.getElementById('monthOrdersChart')
            );
            monthOrdersChart.draw(_monthOrdersData, monthOrdersOptions);

            let _quarterRevenueData = google.visualization.arrayToDataTable(quarterRevenueData);
            let quarterRevenueChart = new google.visualization.PieChart(
                document.getElementById('quarterRevenueChart')
            );
            quarterRevenueChart.draw(_quarterRevenueData, quarterRevenueOptions);
            let _quarterOrdersData = google.visualization.arrayToDataTable(quarterOrdersData);
            let quarterOrdersChart = new google.visualization.ColumnChart(
                document.getElementById('quarterOrdersChart')
            );
            quarterOrdersChart.draw(_quarterOrdersData, quarterOrdersOptions);

            let _annualRevenueData = google.visualization.arrayToDataTable(annualRevenueData);
            let annualRevenueChart = new google.visualization.PieChart(
                document.getElementById('annualRevenueChart')
            );
            annualRevenueChart.draw(_annualRevenueData, annualRevenueOptions);
            let _annualOrdersData = google.visualization.arrayToDataTable(annualOrdersData);
            let annualOrdersChart = new google.visualization.ColumnChart(
                document.getElementById('annualOrdersChart')
            );
            annualOrdersChart.draw(_annualOrdersData, annualOrdersOptions);
        }
    },
    computed: {
        showPrintAction() {
            return this.currentIsActiveIndex === 0;
        },
        todayRevenueIsActive() {
            return this.currentIsActiveIndex === 0 || this.currentIsActiveIndex === 1;
        },
        yesterdayRevenueIsActive() {
            return this.currentIsActiveIndex === 0 || this.currentIsActiveIndex === 2;
        },
        weekRevenueIsActive() {
            return this.currentIsActiveIndex === 0 || this.currentIsActiveIndex === 3;
        },
        monthRevenueIsActive() {
            return this.currentIsActiveIndex === 0 || this.currentIsActiveIndex === 4;
        },
        quarterRevenueIsActive() {
            return this.currentIsActiveIndex === 0 || this.currentIsActiveIndex === 5;
        },
        annualRevenueIsActive() {
            return this.currentIsActiveIndex === 0 || this.currentIsActiveIndex === 6;
        }
    },
    watch: {
        isActiveIndex(newIndex, currentIndex) {
            this.currentIsActiveIndex = newIndex;
        }
    }
}
</script>